import { ElMessageBox } from 'element-plus'
import { ElMessageBoxOptions } from 'element-plus/lib/components/message-box/src/message-box.type';
export class Message {
  public static warn(content: string, title: string) {
    const options: ElMessageBoxOptions = {
      customClass: 'alert-style-1',
      confirmButtonText: 'OK',
      cancelButtonText: '取消',
      showClose: false,
      closeOnClickModal: false,
    };
    ElMessageBox.alert(content, title, options);
  }
}
